import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';

import { PaymentMethodResource } from '@core/http';
import { Organization, PaymentMethod  } from '@data/schema';

import { PaymentMethodCreateComponent } from '../create';

@Component({
  selector: 'payment-methods-list',
  templateUrl: './payment-methods-list.component.html',
  styleUrls: ['./payment-methods-list.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class PaymentMethodsListComponent implements OnInit {
  @Input()
  enableSetDefault: boolean;

  @Input()
  enableCreateCard: boolean;

  @Input()
  organizationId: Organization['id'];

  @Input()
  paymentMethods: Array<PaymentMethod>;

  constructor(
    private cd: ChangeDetectorRef,
    private dialog: MatDialog,
    private paymentMethodResource: PaymentMethodResource,
  ) { }

  ngOnInit(): void {}

// View Access

  openCreateDialog() {
    const dialogRef = this.dialog.open(PaymentMethodCreateComponent, {
      width: '600px',
    });

    const instance = dialogRef.componentInstance;
    instance.organizationId = this.organizationId;

    const onCreateSub = dialogRef.componentInstance.create.subscribe($event => {
      this.renderNewPaymentMethod($event);
    });

    dialogRef.afterClosed().subscribe($event => {
      onCreateSub.unsubscribe();
    });
  }

  selectPaymentMethod(paymentMethod: PaymentMethod) {
    if (paymentMethod.isDefault) return;

    // Set selected payment method as default
    this.paymentMethodResource.update(paymentMethod.id).subscribe(res => {
      this.renderDefaultPaymentMethod(res);
    });
  }

  handlePaymentMethodRemove(paymentMethodId: PaymentMethod['id']) {
    const newPaymentMethods = this.paymentMethods.slice();

    newPaymentMethods.splice(
      this.paymentMethods.findIndex((paymentMethod) => {
        return paymentMethod.id === paymentMethodId;
      }), 1);

    this.paymentMethods = newPaymentMethods;
  }

// Helpers

  renderNewPaymentMethod(paymentMethodResponse) {
    const newPaymentMethods = [...this.paymentMethods];
    const paymentMethod = new PaymentMethod(paymentMethodResponse);

    newPaymentMethods.push(paymentMethod);
    this.paymentMethods = newPaymentMethods;
    
    this.renderDefaultPaymentMethod(paymentMethod);
  }

  renderDefaultPaymentMethod(paymentMethod) {
    const newPaymentMethods = this.paymentMethods.filter(pm => {
      return pm.id !== paymentMethod.id;
    });

    newPaymentMethods.forEach(pm => {
      pm.isDefault = false;
    });
    
    paymentMethod.isDefault = true;
    newPaymentMethods.unshift(paymentMethod);

    this.paymentMethods = newPaymentMethods;

    this.cd.markForCheck();
  }
}
