import { Component, EventEmitter,  Input, Output } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';

import { PaymentMethodResource } from '@core/http/payment-method-resource.service';

import { PaymentMethod } from '@data/schema';

import { PaymentMethodCreateComponent } from '../create';

@Component({
  selector: 'payment-method-card',
  templateUrl: 'payment-method-card.component.html',
  styleUrls: ['payment-method-card.component.scss'],
})
export class PaymentMethodCardComponent {
  @Input() data: PaymentMethod;
  @Input() enableRemove = false;
  @Input() enableEdit = false;

  @Output() remove = new EventEmitter<PaymentMethod['id']>();
  @Output() select = new EventEmitter<PaymentMethod>();

  constructor(
    private dialog: MatDialog,
    private paymentMethodResource: PaymentMethodResource
  ) {}

  ngOnInit() {

  }

// API Access

  edit(paymentmethod: PaymentMethod) {

  }

  destroy(paymentMethod: PaymentMethod) {
    this.paymentMethodResource.destroy(paymentMethod.id).subscribe(res => {
      this.remove.emit(paymentMethod.id);
    });
  }

// View Access

  openEditDialog(paymentMethod: PaymentMethod) {
    const dialogRef = this.dialog.open(PaymentMethodCreateComponent, {
      data: paymentMethod,
      width: '600px',
    });

    const onCreateSub = dialogRef.componentInstance.edit.subscribe($event => {

    });

    dialogRef.afterClosed().subscribe($event => {
      onCreateSub.unsubscribe();
    });
  }
}
